/* global CustomFunctions */

export type Message = { role: "user" | "system" | "assistant"; content: string };

// :   URL   -. 
//     localhost.
const PROXY_URL = "https://excelprx.hsecontest.ru/api/chat"; 

/**
 *      LocalStorage
 */
function getStoredToken(): string {
  const saved = localStorage.getItem("copilot_settings");
  if (saved) {
    const parsed = JSON.parse(saved);
    return parsed.serviceToken || "";
  }
  return "";
}

/**
 *        (   Excel ).
 */
export async function chatStream(
  messages: Message[],
  invocation: { setResult: (res: string) => Promise<void> | void; onCanceled: () => void }
): Promise<void> {
  try {
    const token = getStoredToken();
    if (!token) {
      throw new Error("   .    ()   .");
    }

    const response = await fetch(PROXY_URL, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "Authorization": `Bearer ${token}`
      },
      body: JSON.stringify({ messages, stream: true })
    });

    if (!response.ok) {
      const errorData = await response.json().catch(() => ({}));
      throw new Error(errorData.error || ` : ${response.status}`);
    }

    const reader = response.body?.getReader();
    const decoder = new TextDecoder();
    let fullResponse = "";

    if (reader) {
      for (;;) {
        const { done, value } = await reader.read();
        if (done) break;

        const chunk = decoder.decode(value, { stream: true });
        const lines = chunk.split("\n");

        for (const line of lines) {
          if (line.startsWith("data: ")) {
            const data = line.slice(6);
            if (data.trim() === "[DONE]") continue;

            try {
              const parsed = JSON.parse(data);
              const content = parsed.choices?.[0]?.delta?.content || "";
              if (content) {
                fullResponse += content;
                await invocation.setResult(fullResponse);
              }
            } catch (e) {
              //     JSON-
            }
          }
        }
      }
    }

    if (!fullResponse) {
      throw new Error("    .");
    }
  } catch (error) {
    console.error("Error in chatStream:", error);
    await invocation.setResult(`: ${error instanceof Error ? error.message : " "}`);
  }
}

/**
 *      DC Expert.
 * @customfunction DCEXPERT
 * @param cellValue      .
 * @param query      .
 * @returns      .
 */
export async function dcexpert(cellValue: any, query: string): Promise<string> {
  try {
    const token = getStoredToken();
    if (!token) {
      return ":    .     DC Expert.";
    }

    const systemPrompt = `  DC Expert,   Excel. 
 :  ,           .
 :
1.      ,   .
2.     Markdown ( \`,  *,  #).
3.       ,        Excel.
4.      .`;

    const messages: Message[] =[
      { role: "system", content: systemPrompt },
      { role: "user", content: `  : ${JSON.stringify(cellValue)}\n\n: ${query}` },
    ];

    const response = await fetch(PROXY_URL, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "Authorization": `Bearer ${token}`
      },
      body: JSON.stringify({ messages, stream: false })
    });

    if (!response.ok) {
      const errorData = await response.json().catch(() => ({}));
      return `: ${errorData.error || response.statusText}`;
    }

    const data = await response.json();
    return data.choices?.[0]?.message?.content?.trim() || "   .";
  } catch (error) {
    console.error("Error in dcexpert:", error);
    return `: ${error instanceof Error ? error.message : " "}`;
  }
}

CustomFunctions.associate("DCEXPERT", dcexpert);
CustomFunctions.associate("DCEXPERT", dcexpert);